html{
    background-color:  #181818;
    
}

h1 {
    color: #fff;
    align-self: center;
    margin: 30px;
}



button   {background: #6698cb;
    margin: 40px auto;
    max-width: 300px;
    border-radius: 4px;
    text-align: center;
    padding: 10px 18px;  
  font-size: 14px;
  box-shadow: 0px 4px #74a3b0;
  border: 1px solid rgba(0,0,0,0.21);
    border-bottom-color: rgba(0,0,0,0.34);
    transition: top .01s linear;
	text-shadow: 0 1px 0 rgba(0,0,0,0.15);

}

button:active {
    box-shadow: 0 0 #74a3b0; background-color: #709CA8;
}

input[type="text"] {
    padding: 20px;
    margin: 20px;
    border: grey 2px solid;
    border-radius: 5px;
    display: flex;
    justify-self: center;

}
input[type="file"] {
    padding: 20px;
    margin: 20px;
    border: grey 2px solid;
    border-radius: 5px;

}

.card-body {
    background: #252627;
    width: 1000px;
    height: 600px;
    display: flex;
    justify-self: center;
    margin: 30px;
    padding: 30px;
    border: #231F20;
    color: #FFF;
    border: 1px solid rgba(0,0,0,0.21);
    border-radius: 3px;
    
}

span{
    color: #6698cb;
    align-self: center;
    padding: 20px;
    margin: 5px;
 
   

}
